<script>
export default {
  name: "Header",
  data() {
    let user = storage.get("user");

    return {
      user: user ? user : {},
      message: 2
    };
  },
  methods: {
    // 用户名下拉菜单选择事件
    handleCommand(command) {
      if (command == "loginout") {
        storage.remove("user");
        window.location.href = "/login";
      }
    }
  }
};
